import React from 'react'
import Link from 'next/link'
import MainLayout from '../layout/MainLayout';

class Repo extends React.Component{
    static getInitialProps(){
        return{
            name:"Blue",
            star:100
        }

    }
    render(){
        return(
            <MainLayout>
            <h1>Name:{this.props.name}</h1>
            <h1>Stars: {this.props.star}</h1>
            </MainLayout>
        )
    }
}

export default Repo